<template>
    <div class="">
        <div class="vmc-flex vmc-flex-rstart" style="margin-top: 20px;padding-left: 20px;">
			<router-link class="vmc-block btn-sm" to="/">返回</router-link>
		</div>
        <vmc-uploader :afterRead="afterRead" class="vmc-flex vmc-flex-center vmc-flex-column">
            <i class="ion-camera"></i>
            <img :src="base64url" width="80%"/>
        </vmc-uploader>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                base64url: ''
            }
        },
        methods: {
            afterRead(data) {
                this.base64url = data.content
            }
        }
    }
</script>

<style lang="less" scoped>
    i{
        font-size: 40px;
    }
</style>


